.ProjectsListBox {
    margin-top: 3rem;
    padding-left: 1rem;
}

.ProjectsZone {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.ProjectsStyle {
    color: rgb(231, 27, 71);
    text-shadow: 1px 1px 3px rgb(231, 27, 71);
    font-size: 4rem;
}

.questionStyle {
    display: flex;
    align-items: center;
    color: #F0E7FD;
    text-shadow: 1px 1px 2px #F0E7FD;
}

.ProjectsItemsList {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-wrap: wrap;
    margin-top: 5rem;
    margin-bottom: 5rem;
}

.projectsTitle {
    text-align: center;
    margin: 1rem;
    font-size: 1.5rem;
    text-shadow: 1px 1px 1px #F0E7FD;
}

.type, .matiere {
    text-align: right;
    padding-right: 1.5rem;
}

.projectImage{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
}

.projectImage img {
    width: 13.5rem;
    height: 13.5rem;
}

.flip-card {
    color: #F0E7FD;
    cursor: pointer;
    width: 18rem;
    height: 25rem;
    margin-bottom: 2rem;
    margin: 1rem;
}
  
.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  border-radius: 10px;
}

.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
  box-shadow: 1px 1px 2px crimson, 0 0 10px crimson, 0 0 5px crimson;
}

.flip-card-front, .flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  border: 2px solid #4A4153;
  border-radius: 10px;
  box-shadow: 1px 1px 2px black, 0 0 10px #4A4153, 0 0 5px #4A4153;
}

.flip-card-front {
  background-color: #4A4153;
  color: #F0E7FD;
}

.flip-card-back {
  background-color: #4A4153;
  color: white;
  transform: rotateY(180deg);
  padding: 1rem;
  text-align: left;
  text-indent: 10%;
  width: 15.75rem;
  height: 22.75rem;
}

.firstLink {
  position: absolute;
  bottom: 0.5rem;
  right: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.secondLink {
  position: absolute;
  bottom: 0.5rem;
  right: 5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.pdf {
  padding: 0.75rem;
  border-radius: 25px;
  background-color: #F40F02;
  box-shadow: 1px 1px 2px #F40F02;
}

.download {
  padding: 0.75rem;
  border-radius: 25px;
  background-color: #2E282F;
  box-shadow: 1px 1px 2px #2E282F;
}

.websiteLink {
  color: #F0E7FD;
  padding-left: 0.25rem;
  padding-right: 0.25rem;
}

@media screen and (max-width: 1500px) {
  .ProjectsListBox {
    padding-right: 1rem;
}
}